export default "import React, {Component} from 'react';\nimport {Circle, GoogleMap, InfoWindow, withGoogleMap} from 'react-google-maps';\nimport canUseDOM from 'can-use-dom';\nimport raf from 'raf';\n\nconst geolocation =\n  canUseDOM && navigator.geolocation\n    ? navigator.geolocation\n    : {\n        getCurrentPosition(success, failure) {\n          failure(`Your browser doesn't support geolocation.`);\n        },\n      };\n\nconst GeolocationExampleGoogleMap = withGoogleMap((props) => (\n  <GoogleMap defaultZoom={10} center={props.center}>\n    {props.center && (\n      <InfoWindow position={props.center}>\n        <div>{props.content}</div>\n      </InfoWindow>\n    )}\n    {props.center && (\n      <Circle\n        center={props.center}\n        radius={props.radius}\n        options={{\n          fillColor: 'red',\n          fillOpacity: 0.2,\n          strokeColor: 'red',\n          strokeOpacity: 1,\n          strokeWeight: 1,\n        }}\n      />\n    )}\n  </GoogleMap>\n));\n\n/*\n * https://developers.google.com/maps/documentation/javascript/examples/map-geolocation\n *\n * Add <script src=\"https://maps.googleapis.com/maps/api/js\"></script> to your HTML to provide google.maps reference\n */\nexport default class GeolocationExample extends Component {\n  state = {\n    center: null,\n    content: null,\n    radius: 6000,\n  };\n\n  isUnmounted = false;\n\n  componentDidMount() {\n    const tick = () => {\n      if (this.isUnmounted) {\n        return;\n      }\n      this.setState({radius: Math.max(this.state.radius - 20, 0)});\n\n      if (this.state.radius > 200) {\n        raf(tick);\n      }\n    };\n    geolocation.getCurrentPosition(\n      (position) => {\n        if (this.isUnmounted) {\n          return;\n        }\n        this.setState({\n          center: {\n            lat: position.coords.latitude,\n            lng: position.coords.longitude,\n          },\n          content: `Location found using HTML5.`,\n        });\n\n        raf(tick);\n      },\n      (reason) => {\n        if (this.isUnmounted) {\n          return;\n        }\n        this.setState({\n          center: {\n            lat: 60,\n            lng: 105,\n          },\n          content: `Error: The Geolocation service failed (${reason}).`,\n        });\n      },\n    );\n  }\n\n  componentWillUnmount() {\n    this.isUnmounted = true;\n  }\n\n  render() {\n    return (\n      <GeolocationExampleGoogleMap\n        containerElement={\n          <div className='cr-embed-responsive cr-embed-responsive-21by9' />\n        }\n        mapElement={<div className='cr-embed-responsive-item' />}\n        center={this.state.center}\n        content={this.state.content}\n        radius={this.state.radius}\n      />\n    );\n  }\n}\n";